.pagination {
  margin-bottom: 0;

  .page-item {
    &:not(:last-child) {
      margin-right: 8px;
    }
  }

  .page-link {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 30px;
    height: 30px;
    color: $gray-800;
    background: $light !important;
    border-radius: 50% !important;
    padding: 0;
    font-size: 12px;
    padding-bottom: 2px;

    .fa {
      position: relative;
      top: 1px;
    }
  }

  .page-item.active .page-link {
    background-color: $primary !important;
  }

  @media (max-width: 767px) {
    .page-link {
      padding: 4px 10px;
    }
  }
}
