.section {
  padding: 60px 0;

  &__title {
    font-size: 34px;
    text-align: center;
    color: $primary;
    margin-bottom: 40px;

    span {
      display: inline-block;
      border-bottom: 1px solid $primary;
    }
  }

  &__desc {
    font-size: 18px;
    font-weight: 300;
    line-height: 1.666;
    text-align: center;
    color: $primary;
    margin-bottom: 40px;
  }

  &__content {
    font-size: 18px;
    font-weight: 300;
    line-height: 1.666;
    color: $primary;
  }

  @media (max-width: 1199px) {
    padding: 50px 0;

    &__title {
      font-size: 30px;
      margin-bottom: 30px;
    }

    &__desc {
      font-size: 16px;
    }
  }
}
